
import LinkedIn from "@/components/LinkedIn.vue";
import at from "@/components/at.vue";
import Github from "@/components/Github.vue";
import Stackoverflow from "@/components/Stackoverflow.vue";
import { defineComponent } from "vue";
import { useMq } from "vue3-mq";
import { ref } from "vue";
import { useAppStore } from "@/store/app";
export default defineComponent({
  name: "HomePage",
  components: {
    LinkedIn,
    at,
    Github,
    Stackoverflow,
  },
  created() {
    window.addEventListener("DOMMouseScroll", this.handleMouseWheelDOM); // Mozilla Firefox
    window.addEventListener("mousewheel", this.handleMouseWheel, {
      passive: false,
    }); // Other browsers

    window.addEventListener("touchstart", this.touchStart, {
      passive: false,
    }); // mobile devices
    window.addEventListener("touchmove", this.touchMove, {
      passive: false,
    }); // mobile devices
  },

  setup() {
    const mq = useMq();
    const showMenu = ref(false);
    const appStore = useAppStore();

    /**
         inMove: false,
    activeSection: 0,
    offsets: [],
    touchStartY: 0
     */

    const tabCss = ref(
      "h-10 px-4 py-2 -mb-px text-sm text-center text-gray-700 bg-transparent border-b-2 border-transparent sm:text-base  whitespace-nowrap cursor-base focus:outline-none hover:border-gray-400"
    );
    const ActiveTabCss = ref(
      "h-10 px-4 py-2 -mb-px text-sm text-center text-blue-600 bg-transparent border-b-2 border-blue-500 sm:text-base  whitespace-nowrap focus:outline-none"
    );

    const currentActiveTab = ref("Personal");

    const toggleTab = (tab: string) => {
      currentActiveTab.value = tab;
    };

    const scrollToSection = (id: string) => {
      document.getElementById(id).scrollIntoView({
        behavior: "smooth",
      });
    };

    return {
      mq,
      showMenu,
      tabCss,
      ActiveTabCss,
      currentActiveTab,
      toggleTab,

      scrollToSection,
      appStore,
    };
  },
});
